import { LitElement } from "lit";
import { Constructor } from "./constructor";

export declare class RequesterInterface {
  requestInstance: (key: any) => any;
} 

export const Requester = <T extends Constructor<LitElement>>(superClass: T) => {
  class RequesterClass extends superClass {
    requestInstance(key: any) {

      const event = new CustomEvent('request-instance', {
        detail: { key },
        bubbles: true,
        cancelable: true,
        composed: true
      });
      
      this.dispatchEvent(event);

      const instance = (event as CustomEvent).detail.instance;
      console.log(instance);
      return instance;
    }
  };
  
  return RequesterClass as Constructor<RequesterInterface> & T;
}